import { upperFirst } from 'scule';

async function getLastGitTag() {
  const r = await execCommand("git", ["--no-pager", "tag", "-l", "--sort=taggerdate"]).then((r2) => r2.split("\n"));
  return r[r.length - 1];
}
async function getCurrentGitBranch() {
  const r = await execCommand("git", ["rev-parse", "--abbrev-ref", "HEAD"]);
  return r;
}
async function getGitDiff(from, to) {
  const r = await execCommand("git", ["--no-pager", "log", `${from}...${to}`, '--pretty="----%n%s|%h|%an|%ae%n%b"', "--name-status"]);
  return r.split("----\n").splice(1).map((line) => {
    const [firstLine, ..._body] = line.split("\n");
    const [message, shortHash, authorName, authorEmail] = firstLine.split("|");
    const r2 = {
      message,
      shortHash,
      author: { name: authorName, email: authorEmail },
      body: _body.join("\n")
    };
    return r2;
  });
}
function parseCommits(commits, config) {
  return commits.map((commit) => parseGitCommit(commit, config)).filter(Boolean);
}
const ConventionalCommitRegex = /(?<type>[a-z]+)(\((?<scope>.+)\))?(?<breaking>!)?: (?<description>.+)/i;
const CoAuthoredByRegex = /Co-authored-by:\s*(?<name>.+)(<(?<email>.+)>)/gmi;
const ReferencesRegex = /#[0-9]+/gm;
function parseGitCommit(commit, config) {
  const match = commit.message.match(ConventionalCommitRegex);
  if (!match) {
    return null;
  }
  const type = match.groups.type;
  let scope = match.groups.scope || "";
  scope = config.scopeMap[scope] || scope;
  const isBreaking = Boolean(match.groups.breaking);
  let description = match.groups.description;
  const references = [];
  const matches = description.matchAll(ReferencesRegex);
  for (const m of matches) {
    references.push(m[0]);
  }
  if (!references.length) {
    references.push(commit.shortHash);
  }
  description = description.replace(ReferencesRegex, "").replace(/\(\)/g, "").trim();
  const authors = [commit.author];
  for (const match2 of commit.body.matchAll(CoAuthoredByRegex)) {
    authors.push({
      name: (match2.groups.name || "").trim(),
      email: (match2.groups.email || "").trim()
    });
  }
  return {
    ...commit,
    authors,
    description,
    type,
    scope,
    references,
    isBreaking
  };
}
async function execCommand(cmd, args) {
  const { execa } = await import('execa');
  const res = await execa(cmd, args);
  return res.stdout;
}

function generateMarkDown(commits, config) {
  const typeGroups = groupBy(commits, "type");
  let markdown = "";
  const breakingChanges = [];
  for (const type in config.types) {
    const group = typeGroups[type];
    if (!group || !group.length) {
      continue;
    }
    markdown += "\n\n### " + config.types[type].title + "\n\n";
    for (const commit of group.reverse()) {
      const line = "  - " + (commit.scope ? `**${commit.scope.trim()}:** ` : "") + (commit.isBreaking ? "\u26A0\uFE0F  " : "") + upperFirst(commit.description) + ` (${commit.references.join(", ")})`;
      markdown += line + "\n";
      if (commit.isBreaking) {
        breakingChanges.push(line);
      }
    }
  }
  if (breakingChanges.length) {
    markdown += "\n#### \u26A0\uFE0F  Breaking Changes \n\n";
    markdown += breakingChanges.join("\n");
  }
  let authors = commits.flatMap((commit) => commit.authors.map((author) => formatName(author.name)));
  authors = uniq(authors).sort();
  if (authors.length) {
    markdown += "\n\n### \u2764\uFE0F  Contributors\n\n";
    markdown += authors.map((name) => "- " + name).join("\n");
  }
  markdown += "\n\n----\n\n";
  markdown += `Changes from **${config.from}...${config.to}**
`;
  if (config.github) {
    markdown += `
See all changes: https://github.com/${config.github}/compare/${config.from}...${config.to}
`;
  }
  return markdown.trim();
}
function formatName(name = "") {
  return name.split(" ").map((p) => upperFirst(p.trim())).join(" ");
}
function groupBy(items, key) {
  const groups = {};
  for (const item of items) {
    groups[item[key]] = groups[item[key]] || [];
    groups[item[key]].push(item);
  }
  return groups;
}
function uniq(items) {
  return Array.from(new Set(items));
}

export { getCurrentGitBranch as a, getGitDiff as b, parseGitCommit as c, generateMarkDown as d, getLastGitTag as g, parseCommits as p };
